
.front{
    width:100vw;
    height:90vh;
   
  }
.allfront{
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content: end;
    height:90vh;
    background-color:burlywood;
    background-size:cover;
    
}

.upperfrontcontainer{
    display: flex;
    flex-direction: column;
   align-items: start;
   width:100vw;
   height:95vh;
   justify-content:start;
 

}
.profileandside{
    display: flex;
    justify-content: space-evenly;
}

.frontnav{
    display: flex;
    align-items:center;
    justify-content:start;
    width:100vw;
    height:10vh;
    background-color:darkgrey;
    border-radius: 5px;
   transition: all .9s ;
   
 

}
.navarticle{
    font-size:larger;
    font-style:italic;
    margin-left:45vw;
}
.footer{
    display:flex;
    justify-content: space-evenly;
    height:7vh;
    background-color:midnightblue;
    align-items: center;
    color:white;
    width:100vw;
}
.bodyfront {
    margin-top:10vh;
    display:flex;
}
.bodycontainer{
display:flex;
justify-content: start;
margin-left:20vw;

}
.lowercontainer{
    display:flex;
    justify-content: start;
   
}
.box{
    
    width:25vw;
    height:10vh;
    border-color:white;
    background-color:darkgrey;
    border-radius: 5px;
    margin-top:1vh;;
    transition: all .9s ;
    align-content: center;
    justify-content:center;

}
.box:hover{
    transform:scale(1.1)

}
.fabars{
    transform: rotate(90deg);
    transition-duration: 0.8s;
    margin-left:13vw;
    width:2vw;
    display:none;
}
.nofabars{
    margin-left:13vw;
    transition-duration: 0.8s;
    width:2vw;
    display:none;
}
.sidenav{
    display: flex;
    flex-direction: column;
   align-items: start;
    background-color:greenyellow;
   justify-content:start;
   height:60vh;
   width:45vw;
   display:none;
}
.nosidenav{
display:none;
}
.slider{
   
display: flex;
   width:50vw;
   height:70vh;
   overflow:hidden;
   margin-left:1vw;
 
}
.image{
   
    width:40vw;
    height:40vh;
    margin-top:-40vh;
    position:relative;
    animation-name:four;
    animation-duration: 6s;
    animation-fill-mode: both;
  
}
.profiledetail{
    width:50vw;
    height:90vh;
    background-color:antiquewhite;
   }
.imagereverse{
    width:40vw;
    height:40vh;
    margin-top:-40vh;
    position:relative;
    animation-name:reverser;
    animation-duration: 6s;
    animation-fill-mode: both;
}

@keyframes reverser {
    0%   {left:-45vw;opacity :0.9}
    100% {left:5vw; opacity :0.2 }  
}

@keyframes four {
    0%   {left:5vw;opacity :0.2}
    100% {left:60vw; opacity :0.1; display:'none' }  
}

@media (max-width:800px) {
   
    .nosidenav{
      display:none;
     }
     .sidenav{
        display:none;
     }

     .fabars{
        size:3vw;
        width:4vw;
        display:block;
     }
     .nofabars{
        size:3vw;
        width:4vw;
        display:block;
     }
     .foricondiv:first-child{
        size:30vw;
       
     }
     .sidenav{
         display:flex;
        flex-direction: column;
      align-items:center;
        justify-content:space-evenly;
     }
 
     .nosidenav{
    display: none;
    }
    .list{
        width:20vw;
       
    }
    .list:hover{
        border-bottom-style:solid;
        border-bottom-color:black;
    }
    .navarticle{
        margin-left:30vw;
    }
    .image{
        margin-top:-110vh;
    }
    .imagereverse{
        margin-top:-110vh;
    }
    .profiledetail{
        display:none;
    }
  }
  